@red: #F76F8D;
#booking-form {
  .padding-32();
  background-color: #FFF8F9;
}
.booking__heading {
  text-align: center;
}
.booking__title {
  font-size: 18px;
  .proxima_nova();
  color: #626161;
  text-transform: uppercase;
}
.booking-form {
  .row-flex();
  justify-content: space-around;
}
.input__group-big {
  .col();
  .size(3);
  .container();
  .card();
  .display-container();
  background-color: #FFFFFF;
  box-shadow: 0px 0px 20px 0px rgba(247, 111, 141, 0.15);
 .margin-bottom();
  .size-sm(6);
  .size-xs(8);
  input {
    border: 0;
    outline: none;
    cursor: pointer;
    line-height: 1;
    padding-top: 15px;
    font-size: 14px;
    font-family: "Proxima Nova";
    color: #999999;
  }
  img {
    .display-right();
    right: 10px;
  }

}
.inp-but {
  .col();
  .size(3);
  .button();
  .red();
  .proxima_nova();
  .wide(1px);
  box-shadow: 0px 0px 20px 0px rgba(247, 111, 141, 0.15);
  font-size: 14px;
  color: #FFFFFF;
  text-transform: uppercase;
  .margin-bottom();
  .size-sm(6);
  .size-xs(8);
}
.input__group {
  .col();
  .size(2);
  .container();
  .card();
  .display-container();
  background-color: #FFFFFF;
  box-shadow: 0px 0px 20px 0px rgba(247, 111, 141, 0.15);
  .margin-bottom();
  .size-sm(6);
  .size-xs(8);
  input {
    border: 0;
    outline: none;
    .input-small();
    cursor: pointer;
    line-height: 1;
    padding-top: 15px;
    font-size: 14px;
    font-family: "Proxima Nova";
    color: #999999;
  }

  img {
    .display-right();
    right: 10px;
  }
}