@red:#FF708F;
@pale-red: #FDEFF2;
#footer {
  .padding-16();
  background-color: #fff8f9;
}
.footer__top {
  .row-flex();
  .padding-32();
   justify-content: space-between;
   box-shadow: 0 2px 5px -2px #808080;
  .sm-block({
    justify-content: center;
  });
  .xs-block({
  justify-content: center;
  });
}
.footer__top-item {
  .col();
  .size(3);
  .size-sm(8);
  .size-xs(10);
}
.footer__logo {
  font-family: "Trebuchet MS", Helvetica, sans-serif; 
  .text-red();
  .wide(.5px);
  .padding-64();
  font-size: 2.4rem;

  .sm-block({
  .padding-32();
    text-align: center;
  });

  .xs-block({
  .padding-16();
    text-align: center;
  });
   a{
     text-decoration: none;
     .text-red();
     text-transform: capitalize;

     span {
       text-transform: uppercase;
     }
   }
}
.footer__top-header {
  text-align: left;

  h4 {
    .text-red();
    font-size: 18px;
    font-weight: normal;
    text-transform: capitalize;
  }
}
.footer__top-text{
  .ul();
  text-align: left;

  li {
    padding: 0;
    margin: 0;
    font-size: 14px;
    color: #666666;
    .opacity();
  }
}

.footer__copyright {
  .row-flex();
  .padding-16();
  justify-content: space-between;
  align-items: center;

  .xs-block({
  justify-content: center;
  });
}
.footer__copyright-left {
  .col();
  .size(6);
  .size-xs(10);
  text-align: left;
  .xs-block({
    text-align: center;
  });
  p{
    font-size: 14px;
    color: #666666;
    .opacity();
    font-weight: normal;
    .wide(.5px);
  }
}
.footer__copyright-right {
  .col();
  .size(6);
  .size-xs(10);
  text-align: right;
  .wide(5px);
  .xs-block({
    text-align: center;
  });
  a {
    .button();
    .padding-small();
    .round-large();
  }
}