@red: #F76F8D;


#destination {
  .padding-16();
  .white();
}

.destination__row  {
    .row-flex();

  .sm-block({
    justify-content: center;
  });

  .xs-block({
    justify-content: center;
  });
}
.destination__item {
    .col();
    .size(4);
    .size-sm(8);
    .size-xs(10);
    .margin-bottom();
    .card-4();
  box-shadow: 0px 1px 13px 2px rgba(189, 10, 170, 0.15);
    img {
      width: 100%;
    }
}
.destination__item-big {
  .col();
  .size(8);
  .size-sm(8);
  .size-xs(10);
  .margin-bottom();
  .card-4();
  box-shadow: 0px 1px 13px 2px rgba(189, 10, 170, 0.15);
  img {
    max-width: 100%;
  }
}
