#paling {
  .padding-16();
  .white();
}

.event__form {
  .row-flex();
  justify-content: center;
}
.event__input {
  .col();
  .size(3);
  .size-sm(6);
  .size-xs(10);
  .margin-bottom(32px);
  cursor: pointer;
  transition: all .5s;



  input {
    outline: none;
    .container();
    .input(0px);
    .proxima_nova();
    .round-xxlarge();
    .card();
    cursor: pointer;
    font-size: 14px;
    color: #CCCCCC;
    text-align: center;
    &:hover {
      .card();
      box-shadow: 0px 2px 15px 0px rgba(189, 10, 170, 0.15);
      .text-red();
    }
  }
}

.event__row {
 .row-flex();
  justify-content: center;
}

.event__item {
  .col();
  .size(6);
  .size-sm(10);
  .row-flex();
  justify-content: center;

  .margin-bottom(24px);
}
.event__item-img {

  img {
    max-width: 100%;
  }
}

.event__item-text {
  .container();
  .left-align();
  .wide(1px);

  p {
    .proxima_nova();
    font-size: 16px;
    color: #666666;
    line-height: normal;
  }

}
.event__item-center {
  .padding-24();
  .proxima_nova();
  font-size: 12px;
  color: #999999;
}
.event__item-price {
  .proxima_nova();
  .text-red();
  font-size: 16px;
}