@red:#FF708F;
@pale-red: #FDEFF2;

#bottom__section{
  .padding-24();
  background-color: #fff8f9;
}
.bottom__section-wrap{
  .row-flex();
  justify-content: center;
}
.bottom__section-item {
  .col();
  .size(4);
  .size-md(4);
  .size-sm(8);
  .size-xs(10);
  .card();
  .white();
  .round-medium();
  .padding-large();
  .margin-bottom(24px);
  box-shadow: 0px 1px 13px 2px rgba(189, 10, 170, 0.15);
}
.bottom__section-header {
  .left-align();

  h3, .bottom__section-title {
    .text-red();
    .proxima_nova();
    .wide(1px);
    .text-red();
    .margin-bottom();
    text-transform: uppercase;
  }
  .bottom__section-title {
    text-transform: capitalize;
  }
}
.bottom__section-content {
  .ul();
  .proxima_nova();
  .wide(1px);
  .margin-bottom();

  li, p {
    padding: 0;
    margin: 0;
    .margin-bottom(5px);
    font-weight: 200;
    font-size: 14px;
    color: #666666;
    .opacity-min();
  }
}
.bottom__section-footer {
  text-align: center;

  p {
    .left-align();
    .text-red();
    .opacity();
  }
  .bottom__section-btn {
    .btn();
    .round-small();
    .proxima_nova();
    background-color: #fdeff2;
    font-weight: normal;
    width: 90%;
    .content();
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    color: #666666;
    .opacity();
  }
}