@import "../themes/navigations";
@import "../themes/my2";
@import "smart-grid";
/*=======================================
FONTS
======================================*/
@import "../css/proxima_nova.css";
@import "http://fonts.fontstorage.com/import/lobster.css";


/*=======================================
VARS
=======================================*/
.proxima_nova() {
  font-family: "proxima_nova", sans-serif;
}
.lobster(){
  font-family: "lobster", sans-serif;
}
/*=======================================
SECTIONS
======================================*/
@import "sections/_header";
@import "sections/_booking-form";
@import "sections/_destination";
@import "sections/_events";
@import "sections/_bottom-section";
@import "sections/_footer";
/*=======================================
MAIN STYLES
======================================*/
/*.reset();*/
.wrapper {
  .wrapper();

}

html {
  font-size: 15px;

  body {
    height: 100%;
    .proxima_nova();
    .text-grey();
    line-height: 1.8;
  }

  .md-block({ font-size: 12px; });
  .sm-block({ font-size: 11px; });
  .xs-block({ font-size: 10px; });
}
.section__wrap {
  .row-flex();
  .padding-16();
  justify-content: center;
  align-items: center;

  .section__but {
    .size(3);
    .size-md(4);
    .size-sm(6);
    .size-xs(8);
    .btn();
    .red();
    .round-xxlarge();
    .text-white();
    .proxima_nova();
    box-shadow: 7.5px 12.99px 15px 0px rgba(189, 10, 170, 0.15);
    font-size: 14px;
    text-transform: uppercase;
  }
}

.section__header{
  .row();
  width: 80%;
  .content();
  .proxima_nova();
  text-align: center;

  .section__title{
    .proxima_nova();
    .wide(1px);
    line-height: 1.2;
    font-weight: 200;
    font-size: 30px;
    text-transform: capitalize;
    color: #333333;
  }

  p {
    .wide(1px);
    font-weight: 200;
    color: #cccccc;
    font-size: 18px;
    line-height: 1.45;
  }
}




