#header {

    .card-4();
    .display-container();
    min-height: 790px;
    background-color: #F76F8D;
    background: url(../img/header-bg.jpg) no-repeat top center;
    background-size: cover;
    box-shadow: 7.5px 12.99px 15px 0px rgba(189, 10, 170, 0.15);
    overflow: hidden;

    .sm-block({
    min-height: 390px;
    });
}


.header__top {
    background-color: rgba(0, 0, 0, .149);
}


.header__top-row {
    justify-content: space-between;
    align-items: center;
    .row-flex();
    .sm-block({
    justify-content: center;
    });
}

.logo {
    .col();
    .size(2);
    .size-md(3);
    .size-sm(6);
    text-align: left;

    .sm-block({
        text-align: center;
    });

    a {
        outline: none;
        text-decoration: none;
        font-size: 24px;
        color: #FFFFFF;;
        font-family: "Trebuchet MS", Helvetica, sans-serif;

    }
}


nav {
    .right-align();
    .col();
    .size(10);
    .size-md(9);
    .sm-block({
      .hide();
    });


    .horizontal-menu-simple({
    nav, ul {
      .transparent();
    }

    li {
      .right();
      padding: 14px 0;
    }

    a {
      font-size: 14px;
      font-family: "Proxima Nova";
      color: rgba(255, 255, 255, 0.35);
      .bottombar(2px);
      .border-trnasparent();


       &:hover {
        .transparent();
        .bottombar(2px);
        .border-white();
        .text-white();
      }
    }
    a.active {
      .bottombar(2px);
      .border-white();
    }
    });
}


.header__content{
  width: 70%;
  text-align: center;
  .content();
}


.header__slogan{
    .display-middle();

    .sm-block({
    .display-topmiddle();
      padding-top: 64px;
    });
}


.header__title {
    .proxima_nova();
    .margin-bottom(48px);
    font-size: 3.2rem;
    color: #FFFFFF;
    font-weight: bold;
    line-height: 1.2;
    text-shadow: 2.5px 4.33px 20px rgba(0, 0, 0, 0.3);
    text-transform: uppercase;
}


.header__contacts {
    .display-container();
    .row-flex();
    justify-content: center;
}


.header__input {
    .col();
    .size(7);
    .size-md(7);
    .size-sm(7);
    .round-xxlarge();
    .padding-small();
    .left-align();
    .white();
    .display-middle();

    .xs-block({
    .hide();
    });

    font-size: 14px;
    font-family: "Proxima Nova";
    color: #999999;
    text-transform: capitalize;

    input {
      outline: none;
      border: none;

    }
}


.header__btn {
    .btn();
    .red();
    .size(2);
    .size-md(2);
    .size-sm(3);
    .display-right();
    .round-xlarge();
    .text-white();
    .proxima_nova();

    .xs-block({
      .hide();
    });

    padding-left: 16px;
    padding-right: 16px;
    right: 20%;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
}





